.button {
  font-size: 16px;
  padding: 13px 28px;
  position: relative;
  box-sizing: border-box;
  color: #1792C7;
  background: white;
  background-clip: padding-box;
  border: solid 1px transparent;
  border-radius: 25px;
  text-decoration: none;
}
.button:before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
  margin: -1px;
  border-radius: inherit;
  background: linear-gradient(180deg, #1792C7 0%, #00B8F0 100%);
}
.button:hover {
  box-shadow: 0px 6px 18px 0px #3598FE61;
  transition: 0.5s;
}

.login-button {
  background: linear-gradient(180deg, #1792C7 0%, #00B8F0 100%);
  font-size: 16px;
  font-weight: 600;
  line-height: 24px;
  letter-spacing: 0.443076998px;
  text-align: center;
  border: none;
  border-radius: 25px;
  padding: 13px 40px;
  width: 161px;
  color: #FFFFFF;
  position: absolute;
  left: 275px;
  height: 48px;
}
.login-button:hover {
  box-shadow: 0px 6px 18px rgba(53, 152, 254, 0.38185);
  transition: 0.5s;
}

.shadow {
  box-shadow: 0px 6px 18px rgba(53, 152, 254, 0.38185);
}

