}

@media (min-width: 768px) and (max-width: 1439px) {
  .header__navigation {
    display: flex;
    align-items: center;
    width: 649px;
  }

  .navigation-wrapper.show {
    display: block;
    position: fixed;
    z-index: 20;
    background: #FFFFFF;
    top: 97px;
    right: 0;
    left: 0;
    bottom: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .navigation-toggle {
    margin-right: 19px;
    background-color: transparent;
  }

  .lang-navigation,
.button-wrapper {
    display: none;
  }

  .navigation-wrapper {
    display: none;
  }

  .navigation-toggle {
    position: relative;
    border: none;
    margin-left: auto;
    width: 27px;
    height: 27px;
  }

  .navigation-toggle::before {
    content: "";
    position: absolute;
    background-color: #1399CF;
    cursor: pointer;
    width: 27px;
    height: 4px;
    right: 0px;
    top: 6px;
  }

  .navigation-toggle::after {
    content: "";
    position: absolute;
    background-color: #1399CF;
    cursor: pointer;
    width: 27px;
    height: 4px;
    right: 0;
    top: 15px;
  }

  .active::after {
    transform: rotate(45deg);
    top: 12px;
  }

  .active::before {
    transform: rotate(-45deg);
    top: 12px;
  }

  .site-navigation__item {
    margin-bottom: 32px;
  }

  .site-navigation__link {
    font-size: 24px;
    font-weight: 500;
    line-height: 33px;
    letter-spacing: 0.9599999785px;
    text-align: center;
    color: #555E68;
  }
  .site-navigation__link:hover {
    color: rgba(85, 94, 104, 0.5);
    transition: 0.5s;
  }
}
