.promo__text-slider {
  /* margin: 50px auto; */
  width: 500px;
  height: 248px;
  overflow: hidden;
  position: relative;
}

.text-slide {
  position: absolute;
  animation: round 16s infinite;
  opacity: 0;
  width: 100%;
}

.promo__picture-wrapper {
  width: 500px;
  height: 300px;
  position: relative;
}

.promo__picture {
  position: absolute;
  animation: round 16s infinite;
  opacity: 0;
  width: 100%;
}

.promo__picture--first,
.text-slide--first {
  animation-delay: 12s;
}

.promo__picture--second,
.text-slide--second {
  animation-delay: 8s;
}

.promo__picture--third,
.text-slide--third {
  animation-delay: 4s;
}

.promo__picture--fourth,
.text-slide--fourth {
  animation-delay: 0s;
}

@keyframes round {
  25% {
    opacity: 1;
  }
  40% {
    opacity: 0;
  }
}
